import * as React from 'react'
import { Link } from  'gatsby'

const styles = {
  tag: {
    fontSize: '0.5rem'
  }
}

const NavigationLink = ({ children, to, isPrivate }) => (
  <Link to={to}>
    {children}
    {isPrivate && <span styles={styles.tag}> (private route)</span>}
  </Link>
)

export default NavigationLink
